@import '../../global.scss';

.about{
    background-color: white;
    display: flex;
    height: calc(100vh - 140px);
    position: fixed;
    top: 70px;
    bottom: 70px;

    .left{
        flex: 0.3;
        background-color: rgba(69, 141, 165, 0.909);
        

        .imgContainer{
            width: 800px;
            height: 800px;
            // background-color: rgba(240, 128, 128, 0.764);
            display: flex;
            align-items: center;
            justify-content: center;
            float: right;
            // padding-top: 30px;
            // padding-bottom: 30px;
            

            img{
                height: 95%;
                border-radius: 50%;
            }
        }
    }

    .right{
        flex: 0.7;
        position: relative;
        background-color: rgba(69, 141, 165, 0.909);
        padding-top: 150px;

        .wrapper{
            width: 100%;
            height: 100%;
            padding-left: 50px;
            display: flex;
            flex-direction: column;
            // justify-content: center;
            padding-top: 150px;
            // margin-right: 50px;
            

            .paragraph{
                margin-right: 50px;
                padding-right: 50px;

                a{
                    text-decoration: none;
                    font-weight: bold;
                    color: inherit;
                    cursor: pointer;
                }
            }

            h2{
                font-size: 40px;
                font-weight: 500;
            }
            h1{
                font-size: 70px;
                margin: 15px 0;
                font-weight: 600;
            }
            h3{
                font-size: 30px;
            }
            p{
                font-size: 25px;
                font-weight: 300;
                margin: 30px 0;
                margin-right: 30px;
            }

            .containerSection{
                padding-top: 20px;
                display: flex;
                align-items: start;

                .leftSection{
                    flex: 0.4;

                    a{
                        text-decoration: none;
                    }
    
                    .button{
                        background-color: rgba(18, 91, 139, 0.962);
                        color: white;
                        borderRadius: 2;
                    }
                }

                .middleSection{
                    flex: 0.4;

                    ul{
                        padding-top: 10px;
                        font-size: 25px;
                        font-weight: 400;
                    }
                }

                .rightSection{
                    flex: 0.4;

                    ul{
                        padding-top: 10px;
                        font-size: 25px;
                        font-weight: 400;
                    }

                }
            }
            

        }

    }
}