@import '../../global.scss';

.header{
    width: 100%;
    height: 70px;
    background-color: rgba(18, 91, 139, 0.962);
    color: white;
    top: 0;
    position: fixed;
    z-index: 2;

    .wrapper{
        padding: 10px 30px;
        display: flex;
        align-items: center;
        justify-content: space-between;

        .left{
            display: flex;
            align-items: center;

            .logo{
                font-size: 50px;
                font-weight: 700;
                text-decoration: none;
                color: inherit;
                margin-right: 40px;
            }

            .menuContainer{
                display: flex;
                align-items: center;
                margin-left: 30px;

                .menuItem{
                    font-size: 20px;
                    font-weight: 300;
                    text-decoration: none;
                    color: inherit;
                }
            }

        }


        
        .right{


            .itemContainer{
                display: flex;
                align-items: center;
                justify-content: center;
                margin-left: 30px;

                .icon{
                    font-size: 30px;
                    margin-right: 3px;
                    color: white;
                }

                span{
                    font-size: 20px;
                    font-weight: 300;
                    margin-left: 10px;
                }

                #mail{
                    text-decoration: none;
                }
    


            }
    
            
            
        }

        

    }
}

